@import "../../_variables";
@import "../../_mixin";

.mainscreen {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-top: 8%;

  background: $BODYCOLOR;
}
.mainscreen-img {
  img {
    width: 350px;
    height: 350px;
    @media (max-width: 480px) {
      width: 250px;
      height: 250px;
    }
  }
}
.mainscreen-content {
  display: flex;
  flex-direction: column;
  h1 {
    font-weight: 900;
    @include font(45px);
    text-align: center;
    margin-bottom: 15%;
    font-family: $HEADFONT;
    color: $FONTCOLOR;
    @media (max-width: 980px) {
      @include font(35px);
    }
    @media (max-width: 480px) {
      @include font(30px);
    }
  }
  .about-link {
    color: $BODYCOLOR;
    padding: 0 5px;
    font-weight: 700 !important;
    background: $FONTCOLOR;
    @include mainscreen-btn;
    width: 250px;
    text-decoration: none;
    margin: 0 auto;
    margin-bottom: 20px;
    @include font(16px);
    @include transition;
    &:hover {
      border: 4px solid $ACCENTCOLOR_HOVER;
    }
  }
}
